import { get, post } from '@Green-Dot-Corporation/eureka-lib-fetch-utils';
import { v4 as uuidv4 } from 'uuid';

export default class GatewayHttpClient {
    // Set the accessToken at the first place
    static accessToken = '';

    static applicationId = '';

    static prefixAPIUrl = '';

    static #addTokenOptions(options) {
        const tokenAddedHeader = GatewayHttpClient.setHeaderOptions(
            options,
            'Authorization',
            `Bearer ${GatewayHttpClient.accessToken}`,
        );
        const applicationIdAddedHeader = GatewayHttpClient.setHeaderOptions(
            tokenAddedHeader,
            'Application-ID',
            GatewayHttpClient.applicationId,
        );
        const guid = uuidv4();
        const requestIdAddedHeader = GatewayHttpClient.setHeaderOptions(
            applicationIdAddedHeader,
            'Request-ID',
            guid,
        );

        return requestIdAddedHeader;
    }

    static setHeaderOptions(options, headerName, value) {
        const { headers } = options ?? {};
        const headerOptions = { ...headers };
        headerOptions[headerName] = value;
        const addedHeader = { ...options };
        addedHeader.headers = headerOptions;

        return addedHeader;
    }

    static get = async (url, options) => {
        const tokenAddedOptions = GatewayHttpClient.#addTokenOptions(options);
        const resp = await get(
            `${GatewayHttpClient.prefixAPIUrl}${url}`,
            tokenAddedOptions,
        );

        return resp;
    };

    static post = async (url, body, options) => {
        const tokenAddedOptions = GatewayHttpClient.#addTokenOptions(options);
        tokenAddedOptions.body = body;
        const resp = await post(
            `${GatewayHttpClient.prefixAPIUrl}${url}`,
            tokenAddedOptions,
        );

        return resp;
    };
}
