import { Client } from '@twilio/conversations';
import { TwilioClientState } from './const/variantsConst';

export default class TwilioClient {
    static client = undefined;

    static conversation = undefined;

    static createClient = (twilioToken) => {
        return new Promise((resolve, reject) => {
            this.client = new Client(twilioToken);
            this.client.on(Client.stateChanged, (state) => {
                if (state === TwilioClientState.INITIALIZED) {
                    resolve(this.client);
                }

                if (state === TwilioClientState.FAILED) {
                    reject(state);
                }
            });
        });
    };

    static getConversationBySid = async (sid) => {
        if (!this.client) {
            throw new Error('twilio client must be initialized.');
        }
        this.conversation = await this.client.getConversationBySid(sid);

        return this.conversation;
    };

    static refreshToken = async (token) => {
        this.client = await this.client.updateToken(token);
    };

    static sendMessage = async (message) => {
        const messageIndex = await this.conversation
            .prepareMessage()
            .setBody(message.body)
            .setAttributes({ index: message.index })
            .build()
            .send();

        return messageIndex;
    };

    static sendMediaMessage = async (message) => {
        const messageIndex = await this.conversation
            .prepareMessage()
            .setAttributes({ index: message.index })
            .addMedia(message.body)
            .build()
            .send();

        return messageIndex;
    };
}
